import $ from 'jquery';
import Vue from 'vue';
import Init from './init';
import {store} from './store/index';

$(document).ready(function () {
    const fastOrderInstance = document.getElementById('fast-order-container');
    const params = JSON.parse(fastOrderInstance.dataset.params);
    const instance = new Vue({
        store: store,
        render: h => h(Init, {
            props: {
                configMage: params
            }
        })
    });
    instance.$mount(fastOrderInstance);
});



