<template>
  <fragment>
    <div :class="stylesGrid.wrapRow" data-row-data="1">
      <div :class="stylesGrid.wrapImage">
        <img
          v-if="item.image"
          :src="mixRenderProductImage(item)"
        >
      </div>
      <div
        :class="stylesGrid.wrapProductName"
        data-product-type="grouped"
        :data-option-grouped-open="dataGroupedOpen"
      >
        <div :class="stylesGrid.wrapGroupedShow">
          <a
            target="_blank"
            :href="item.product_url"
            v-html="item.name"
            :class="stylesGrid.wrapName"></a>
          <span :class="stylesGrid.wrapSku">{{ item.sku }}</span>
        </div>
        <div
          v-for="(itemObj, index) in item.fast_grouped_products"
          :key="index"
        >
          <div :class="stylesGrid.wrapGroupProduct">
            <div :class="stylesGrid.wrapGroupedImage">
              <img
                v-if="itemObj.image"
                :src="mixRenderProductImage(itemObj)"
              >
            </div>
            <div :class="stylesGrid.wrapGroupedProductName">
              <a>{{ itemObj.name }}</a><br>
              <p :class="stylesGrid.price">
                {{ itemObj.price }}
              </p>
            </div>
            <div :class="stylesGrid.wrapGroupedPrice">
              <div
                :class="stylesGrid.wrapQty"
                data-remove-width="1"
              >
                <div
                  :class="stylesGrid.countDown"
                  @click="groupCounterDownQty(itemObj.qty, index)"
                >
                  <span>-</span>
                </div>
                <div :class="stylesGrid.wrapInput">
                  <input
                    :value="itemObj.qty"
                    style="width: 50px"
                    type="text"
                    onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                    @input="qtySimpleProductChange($event, index)"
                  >
                </div>
                <div
                  :class="stylesGrid.countUp"
                  @click="groupCounterUpQty(itemObj.qty, index)"
                >
                  <span>+</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        :class="stylesGrid.wrapSku"
        :data-option-grouped-open="dataGroupedOpen"
        data-product-type="grouped"
      >
        {{ item.sku }}
      </div>
      <div :class="stylesGrid.wrapQty" />
      <div :class="stylesGrid.wrapSubTotal">
        {{ sumPrice() }}
      </div>
      <action
        :item="item"
        :index="index"
      />
    </div>
  </fragment>
</template>

<script>
    import fastOrderMixin from '../fast-order-mixin';
    import Currency from 'currency.js';
    import stylesGrid from '../grid.scss';
    import Action from '../common/Action';
    import Qty from '../common/Qty';

    export default {
        name: "GroupProduct",
        components: {
            'action': Action,
            'qty': Qty
        },
        mixins: [fastOrderMixin],
        props: {
            item: {
                type: Object,
                default: function () {
                    return {}
                }
            },
            index: {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                stylesGrid,
                dataGroupedOpen: true
            }
        },
        methods: {
            qtySimpleProductChange(e, simpleProductIndex) {
                const value = e.target.value;
                // update to mutation
                this.$store.dispatch('qtySimpleProductChange', {productIndex: this.index, simpleProductIndex, value});
            },
            sumPrice() {
                let finalPrice = 0;
                this.item.fast_grouped_products.forEach(item => {
                    if (item.qty > 0) {
                        finalPrice = Currency(item.base_price_value, {formatWithSymbol: true}).multiply(item.qty).add(finalPrice);
                    }
                });
                return this.formatCurrency(finalPrice);
            },
            groupCounterUpQty(qty, index) {
                const qtyCountUp = qty + 1;
                if (qtyCountUp > -1) {
                    this.$store.dispatch('qtySimpleProductChange', {
                        productIndex: this.index,
                        simpleProductIndex: index,
                        value: qtyCountUp
                    });
                }
            },
            groupCounterDownQty(qty, index) {
                const qtyCountUp = qty - 1;
                if (qtyCountUp > -1) {
                    this.$store.dispatch('qtySimpleProductChange', {
                        productIndex: this.index,
                        simpleProductIndex: index,
                        value: qtyCountUp
                    });
                }
            }
        }
    }
</script>



