<template>
  <div
    :data-show-image="enabledSearchDisplayProductImage()"
    :class="styles.wrapSearchItem"
  >
    <div :class="styles.wrapSearchImage">
      <img :src="mixRenderProductImage(item)">
    </div>
    <div :class="styles.wrapSearchInfo">
      <span
        :class="styles.wrapResultName"
        v-html="item.name"
      />
      <span :class="styles.rapResultPrice">
        {{ item.price }}
      </span>
      <span :class="styles.wrapResultSKu">
        {{ item.sku }}
      </span>
      <span
        :class="styles.wrapLoadingSearchItem"
        :data-loading-index="index"
      >
        <div :class="styles.ldsDualRing" />
      </span>
    </div>
  </div>
</template>

<script>
    import styles from './search-box-result.scss';
    import fastOrderMixin from "../../fast-order-mixin";
    import {mapState} from "vuex";

    export default {
        name: "SearchItemVue",
        mixins: [fastOrderMixin],
        props: ['item', 'index'],
        data() {
            return {
                styles
            }
        },
        computed: {
            ...mapState({
                settings: state => state.settings
            })
        },
        methods: {
            enabledSearchDisplayProductImage() {
                const searchSettings = this.settings[2];
                return searchSettings ? searchSettings.display_image_product : 0;
            },
        }
    }
</script>


