import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from './actions';
import mutations from './mutations';
import createLogger from 'vuex/dist/logger';
import Fragment from 'vue-fragment';

Vue.use(Vuex);
Vue.use(Fragment.Plugin);

const pluginsLib = process.env.NODE_ENV !== 'production' ? [createLogger()] : [];
export const store = new Vuex.Store({
    state: {
        domain: '',
        isLoadingCsvUploading: false,
        productList: [], // Main product list to display,
        searchResults: [], // Contain search result
        searchResultsOption: [], // Contain search result for option
        searchLoading: false,
        searchLoadingByOption: false,
        settings: {}, // Basic settings from rest api
        config: { // Config pass from mage-init
            translate: {
                text_action: "",
                text_add_from_file: "",
                text_add_to_cart: "",
                text_add_to_list: "",
                text_checkout: "",
                text_choose_file: "",
                text_download_a_samle_csv_file: "",
                text_enter_multiple_skus: "",
                text_enter_multiple_skus_note: "",
                text_fastorder: "",
                text_products: "",
                text_qty: "",
                text_sku: "",
                text_sub_total: "",
                text_subtotal: "",
                text_total_qty: ""
            },
            filterAttribute: null
        },
        isAddToListProcessing: false,
        isAddingToCartProcessing: false,
        isCheckoutProcessing: false,
        isUploadCsvProcessing: false,
        currencyCode: ''
    },
    actions,
    mutations,
    plugins: pluginsLib
});



