<template>
  <fragment>
    <div
      :class="stylesGrid.wrapRow"
      data-row-data="1"
    >
      <div :class="stylesGrid.wrapImage">
        <img
          v-if="item.fast_product_active.image"
          :src="mixRenderProductImage(item.fast_product_active)"
        >
      </div>
      <div :class="stylesGrid.wrapProductName">
        <a
          target="_blank"
          :href="item.product_url"
          v-html="item.name"
        />
        <div
          :class="stylesGrid.wrapConfOption"
          data-show="desktop"
        >
          <configurable-option
            :product="item"
            :product-index="index"
          />
        </div>
      </div>
      <div :class="stylesGrid.wrapSku">
        {{ item.sku }}
      </div>
      <div
        :class="stylesGrid.wrapConfOption"
        data-show="mobile"
      >
        <configurable-option
          :product="item"
          :product-index="index"
        />
      </div>
      <qty
        :index="index"
        :item="item"
      />
      <div :class="stylesGrid.wrapSubTotal">
        {{ sumPrice() }}
      </div>
      <action
        :item="item"
        :index="index"
      />
    </div>
  </fragment>
</template>

<script>
    import fastOrderMixin from '../fast-order-mixin';
    import configurableOption from '../product-list/ConfigurableOption';
    import Currency from 'currency.js';
    import stylesGrid from '../grid.scss';
    import Action from '../common/Action';
    import Qty from '../common/Qty';

    export default {
        name: "ConfigurableProduct",
        components: {
            'configurable-option': configurableOption,
            'action': Action,
            'qty': Qty
        },
        mixins: [fastOrderMixin],
        props: ['item', 'index'],
        data() {
            return {
              stylesGrid
            }
        },
        methods: {
          sumPrice() {
            const finalPrice = Currency(this.item.fast_product_active.base_price_value, {formatWithSymbol: true}).multiply(this.item.fast_csv_qty);
            return this.formatCurrency(finalPrice);
          }
        }
    }
</script>



