<template>
  <div
    :id="styles.wrapSearchBox"
  >
    <div :class="styles.wrapLineSearchBox">
      <div :class="styles.wrapInputSearch">
        <div :class="styles.wrapLabel">
          <label>Keyword:</label>
        </div>
        <input
          id="wrap-input-search1"
          v-model="searchValue"
          class="wrap-input-search"
          :class="styles.inputSearch"
          placeholder="Search product"
          @focus="searchInputFocus()"
          @keyup.enter="formSearchOnEnter()"
          @input="onChangeSearch()"
        >
        <div
          v-show="this.searchLoading"
          :class="styles.ldsDualRing">
        </div>
        <div id="wrap-input-search-result1">
          <search-box-result :searchResults="searchResults" v-show="isShowSearchResultPanel"/>
        </div>
      </div>
      <div
        v-if="enableSearchOption()"
        :class="styles.wrapInputSearch"
        data-search-box-number="2"
      >
        <div :class="styles.wrapLabel">
          <label>{{config.filterAttribute.filter_label}}</label>
        </div>
        <input
          id="wrap-input-search2"
          v-model="searchValueOption"
          :style="getCustomWidthFilterAttribute()"
          class="wrap-input-search"
          :class="styles.inputSearch"
          :placeholder=config.filterAttribute.filter_placeholder
          @input="onChangeSearchOption()"
          @keyup.enter="formSearchOnEnterOption()"
          @focus="searchInputFocus(1)"
        >
        <div
          v-show="this.searchLoadingByOption"
          :class="styles.ldsDualRing">
        </div>
        <div id="wrap-input-search-result2">
          <search-box-result :searchResults="searchResultsOption" v-show="isShowSearchResultPanelOption"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    import styles from './search-product.scss';
    import {mapState} from 'vuex';
    import fastOrderMixin from '../fast-order-mixin';
    import SearchBoxResult from "./search-box-result/SearchBoxResult";
    import $ from "jquery";

    export default {
        name: "SearchProduct",
        components: {
            'search-box-result': SearchBoxResult
        },
        mixins: [fastOrderMixin],
        data() {
            return {
                styles,
                searchTimeout: null,
                searchTimeoutOption: null,
                searchValue: '',
                searchValueOption: '',
                isShowSearchResultPanel: false,
                isShowSearchResultPanelOption: false
            }
        },
      
        computed: {
            ...mapState({
                productList: state => state.productList,
                domain: state => state.domain,
                searchLoading: state => state.searchLoading,
                searchLoadingByOption: state => state.searchLoadingByOption,
                settings: state => state.settings,
                searchResults: state => state.searchResults,
                searchResultsOption: state => state.searchResultsOption
            })
        },
        mounted() {
          this.addEventCloseSearchResult();
        },
        methods: {
            getCustomWidthFilterAttribute(){
              return {
                width: this.config.filterAttribute.filter_input_width
              }
            },
            formSearchOnEnter() {
                // Call search handle
                this.searchProcessing();
            },
            formSearchOnEnterOption() {
              this.searchProcessingOption();
            },
            enableAutoSearchMinimumCharacter(searchOption = null) {
                const searchSettings = this.settings[2];
                const autoSearchMinimumCharacter = parseInt(searchSettings.autosearch_minimum_character);
                if(searchOption) {
                  return this.searchValueOption.length >= autoSearchMinimumCharacter;
                } else {
                  return this.searchValue.length >= autoSearchMinimumCharacter;
                }
            },
            enableSearchOption() {
              if(this.config.filterAttribute) {
                return 1;
              }
              return 0;
            },
            enableInstantSearch() {
              const generalSettings = this.settings[0];
              const enabledInstantSearch = (generalSettings && generalSettings.enable_instant_search) ? generalSettings.enable_instant_search : 0;
              return parseInt(enabledInstantSearch);
            },
            /**
             * On typing on search input
             */
            onChangeSearch() {
                const enabledInstantSearch = this.enableInstantSearch();
                if (enabledInstantSearch === 1 && this.enableAutoSearchMinimumCharacter()) {
                    this.searchProcessing();
                }
            },
            onChangeSearchOption() {
              const enabledInstantSearch = this.enableInstantSearch();
              if (enabledInstantSearch === 1 && this.enableAutoSearchMinimumCharacter(1)) {
                this.searchProcessingOption();
              }
            },
            searchProcessing() {
                // Show search result panel
                this.showSearchResultPanel();
                // Show loading
                this.$store.dispatch('startLoadingSearch');
                window.clearTimeout(this.searchTimeout);
                const timer = window.setTimeout(() => {
                    this.$store.dispatch('searchAction', this.searchValue);
                }, 1000);
                this.$set(this, 'searchTimeout', timer);
            },
            searchProcessingOption() {
              // Show search result panel
              this.showSearchResultPanelOption();
              // Show loading search option
              this.$store.dispatch('startLoadingSearchOption');
              window.clearTimeout(this.searchTimeoutOption);
              const timer = window.setTimeout(() => {
                this.$store.dispatch('searchActionOption', {searchValue: this.searchValueOption, filterAttribute: this.config.filterAttribute.attribute_code});
              }, 1000);
              this.$set(this, 'searchTimeoutOption', timer);
            },
            /**
             * Show search result
             */
            searchInputFocus(searchOption = null) {
              if(searchOption) {
                if (this.searchValueOption !== '') {
                  // show search result container
                  this.showSearchResultPanelOption();
                }
              } else {
                if (this.searchValue !== '') {
                  // show search result container
                  this.showSearchResultPanel();
                }
              }
            },
            showSearchResultPanel() {
                this.$set(this, 'isShowSearchResultPanel', true);
            },
            showSearchResultPanelOption() {
              this.$set(this, 'isShowSearchResultPanelOption', true);
            },
            addEventCloseSearchResult() {
              const wm = this;
              $(document).mouseup(function (e) {
                if ($(e.target).closest('#wrap-input-search1, #wrap-input-search-result1').length === 0) {
                  // hide search result container
                  wm.hideSearchResultPanel();
                }

                if ($(e.target).closest('#wrap-input-search2, #wrap-input-search-result2').length === 0) {
                  // hide search result container
                  wm.hideSearchResultPanelOption();
                }
              });
            },
            hideSearchResultPanel() {
              this.$set(this, 'isShowSearchResultPanel', false);
            },
            hideSearchResultPanelOption() {
              this.$set(this, 'isShowSearchResultPanelOption', false);
            }
        }
    }
</script>



