/**
 * get product list option
 * @param item
 * @param optionsActive
 * @param defaultOption
 */
export function getProductByListOptions(item, optionsActive, defaultOption = true)
{
    const itemAssign = Object.assign({}, item);
    const optionActiveAssign = [...optionsActive];
    const usedProducts = itemAssign.fast_used_products;

    for (let i = 0; i < usedProducts.length; i++) {
        const product = usedProducts[i];
        const optionLength = optionActiveAssign.length;

        // reset fast_matchOption for next loop
        product.fast_matchOption = 0;

        /**
         * foreach eg: {name: 'size', optionsActive: ['xs', 's']}
         */
        optionActiveAssign.forEach(item => {
            let selectedOptionId;
            selectedOptionId = item.fast_selected;

            if (product[item.fast_attribute_code] === selectedOptionId) {
                product.fast_matchOption = product.fast_matchOption ? product.fast_matchOption + 1 : 1;
            }
        });

        // product meet with option length
        if (product.fast_matchOption === optionLength) {
            return product;
        }
    }
}

/**
 *
 * @param item
 * @returns {[]}
 */
export function findActiveAttrOptions(item)
{
    const attrArrays = item.fast_attribute_array;
    const usedProduct = item.fast_used_products;
    const activeAttrArray = [];

    for (let i = 0; i < attrArrays.length; i++) {
        const item = attrArrays[i];

        // init array keep option item
        const tempOptionParent = {fast_frontend_label: item.frontend_label, fast_attribute_code: item.attribute_code};

        const options = item.values;
        const optionCode = item.attribute_code;

        let optionAvailable = {};
        for (let j = 0; j < options.length; j++) {
            optionAvailable = {};
            const optionItem = options[j];
            const optionId = optionItem.value_index;
            const optionValue = optionItem.label;

            // check in list product
            for (let u = 0; u < usedProduct.length; u++) {
                const product = usedProduct[u];
                if (product[optionCode] && product[optionCode] === optionId.toString() && !optionAvailable[optionId]) {
                    // check exists
                    optionAvailable[optionId] = optionValue;
                    break;
                }
            }

            // is active attribute option
            if (Object.entries(optionAvailable).length > 0) {
                // check if have no options
                if (!tempOptionParent.fast_options) {
                    tempOptionParent.fast_options = [];
                }
                tempOptionParent.fast_options.push(optionAvailable);
            }
        }

        // if attribute option have active in some product
        if (tempOptionParent.fast_options.length > 0) {
            // set default active option
            tempOptionParent.fast_selected = Object.keys(tempOptionParent.fast_options[0])[0];
            // add active selected for select option used
            activeAttrArray.push(tempOptionParent);
        }
    }

    return activeAttrArray;
}


