import $ from 'jquery';
import * as types from './constants'
import {findActiveAttrOptions, getProductByListOptions} from '../helpers/configurable-helper';
import {getAllUrlParams} from "../helpers/common-helper";
import {createOrderAPI, getSettingsAPI, getCurrencyAPI, basePathDev} from './service';
import {RECEIVED_CONFIG} from "./constants";

/**
 * get store id
 * @returns {*|jQuery|string|undefined}
 */
function getStoreCode() {
    let storeCode = $('#fast-order-store-code').val();
    if(storeCode == '' || !storeCode){
        storeCode = 'default';
    }
    return storeCode;
}

/**
 * get default path
 * @returns {string}
 */
function getDefaultPath() {
    const localParam = getAllUrlParams().local;
    let defaultPath = '/loffastforder/fastorder/';
    if (localParam == 1) {
        return `${basePathDev}${defaultPath}`
    }

    return defaultPath;
}

/**
 * set empty productList in local storage
 */
function clearProductListFromLocalStorage() {
    window.localStorage.setItem(types.FAST_ORDER_LOCAL_STORAGE, JSON.stringify([]));
}

/**
 *
 * @returns {string}
 */
function getDefaultAPIPath() {
    const localParam = getAllUrlParams().local;
    const storeCode =  getStoreCode();
    const defaultAPIPath = '/rest/'+storeCode+'/V1/lof-fastorder/';
    if (localParam == 1) {
        return `${basePathDev}${defaultAPIPath}`
    }

    return defaultAPIPath;
}

/**
 * Load all init data
 */
export function bootstrap({commit}, payload) {
    // get settings api
    getSettings(commit, payload);

    // get currency
    getCurrency(commit);

    // get domain url
    const domain = window.location.hostname;
    commit(types.UPDATE_DOMAIN, {domain});
}

/**
 * get currency
 * @param commit
 */
function getCurrency(commit) {
    getCurrencyAPI(response => {
        commit(types.RECEIVED_CURRENCY, response);
    });
}

/**
 * get settings api
 */
function getSettings(commit, payload) {
    commit(types.RECEIVED_CONFIG, payload);
    getSettingsAPI(response => {
        commit(types.RECEIVED_SETTINGS, {response});
    });
}

/**
 * upload file
 * @param commit
 * @returns {Promise<void>}
 */
export async function uploadCsvFile({commit}) {
    const imageFile = document.querySelector('#csv_file');
    const formData = new FormData();
    formData.append('csv_file', imageFile.files[0]);
    formData.append('storeCode', getStoreCode());
    $.ajax({
        url: `${getDefaultPath()}uploadcsv`,
        type: 'POST',
        data: formData,
        async: false,
        cache: false,
        contentType: false,
        enctype: 'multipart/form-data',
        processData: false,
        success(response) {
            if (response.product) {
                const makeStandard = findActiveProductConf(response.product);
                commit(types.SET_TO_PRODUCT_LIST, {productList: makeStandard});

                // stop loading
                commit(types.UPDATE_LOADING_UPLOAD_FILE, false);

                // reset form input
                $('#csv_file').val('');
            }
        },
        error(XMLHttpRequest, textStatus, errorThrown) {
            console.log('error log');
        },
    });
}

/**
 * Update qty by item
 * @param item
 * @param payload
 */
export function updateQty({commit}, payload) {
    commit(types.UPDATE_QTY_BY_ITEM, payload);
}

/**
 * update qty bundle product
 * @param commit
 * @param payload
 */
export function updateQtyBundleProduct({commit}, payload) {
    commit(types.UPDATE_QTY_BUNDLE_PRODUCT, payload);
}

/**
 * remove item
 * @param commit
 * @param payload
 */
export function removeItem({commit}, payload) {
    commit(types.REMOVE_ITEM, payload);
}

/**
 * duplicate product item
 * @param commit
 * @param payload
 */
export function duplicateItem({commit}, payload) {
    commit(types.DUPLICATE_ITEM, payload);
}

/**
 * apply test item
 * @param commit
 * @returns {Promise<void>}
 */
export async function applyTestData({commit}) {
    const testData = [];
    const makeStandard = findActiveProductConf(testData);
    commit(types.SET_TO_PRODUCT_LIST, {productList: makeStandard});
}

/**
 * find active option product for configurable
 */
function findActiveProductConf(productList) {
    // find active attribute first
    for (let i = 0; i < productList.length; i++) {
        const product = productList[i];
        if (product.type_id === 'configurable') {
            const activeOptions = findActiveAttrOptions(product);
            productList[i].fast_active_options = activeOptions;
            productList[i].fast_product_active = getProductByListOptions(product, activeOptions, true);
        }
    }
    return productList;
}

/**
 * configuration on change selected
 * @param commit
 * @param state
 * @param payload
 */
export async function configurationOnChangeSelected({commit, state}, payload) {
    // change selected option
    commit(types.CONFIGURATION_ON_CHANGE_SELECTED, payload);

    const product = state.productList[payload.productIndex];
    const activeOptions = state.productList[payload.productIndex].fast_active_options;

    payload.productActive = getProductByListOptions(product, activeOptions, false);

    // reload product active
    commit(types.RELOAD_CONFIGURATION_OPTIONS_BY_PRODUCT_ID, payload);
}

/**
 * product bundle update selected option value
 * @param commit
 * @param payload
 */
export function productBundleUpdateSelected({commit}, payload) {
    commit(types.PRODUCT_BUNDLE_UPDATE_SELECTED, payload);
}

/**
 *
 * @param commit
 * @param payload
 * @param index index of search item, pass id to stop loading search item when action is done
 */
export function addMultipleProductSku({commit}, payload) {
    const formDataCart = new FormData();
    // push row default as cvs
    formDataCart.append('param', payload.data);
    formDataCart.append('storeCode', getStoreCode());

    $.ajax({
        url: getDefaultAPIPath() + 'fastaddmultiplesku',
        type: 'POST',
        data: formDataCart,
        cache: false,
        processData: false,
        contentType: false,
        success: function (response) {
            // stop loading
            commit(types.UPDATE_LOADING_ADD_TO_LIST, false);

            if(payload.index !== undefined) {
                $(`span[data-loading-index=${payload.index}]`).hide();
            }

            // push data to product list
            const makeStandard = findActiveProductConf(response);
            commit(types.PUSH_TO_PRODUCT_LIST, makeStandard);
        }
    });
}

/**
 * start loading add to list
 * @param commit
 */
export function startLoadingAddToList({commit}) {
    commit(types.UPDATE_LOADING_ADD_TO_LIST, true);
}

/**
 * update loading add to cart
 * @param commit
 */
export function startLoadingAddToCart({commit}) {
    commit(types.UPDATE_LOADING_ADD_TO_CART, true);
}

export function startLoadingCheckout({commit}) {
    commit(types.UPDATE_LOADING_CHECKOUT, true);
}

/**
 * start loading search
 * @param commit
 * @param payload
 */
export function startLoadingSearch({commit}) {
    commit(types.UPDATE_SEARCH_LOADING, true);
}

export function startLoadingSearchOption({commit}) {
    commit(types.UPDATE_SEARCH_LOADING_OPTION, true);
}

/**
 * start loading upload file
 * @param commit
 */
export function startLoadingUploadFile({commit}) {
    commit(types.UPDATE_LOADING_UPLOAD_FILE, true);
}

/**
 * search action
 * @param commit
 * @param payload
 */
export function searchAction({commit}, payload) {
    $.ajax({
        url: getDefaultAPIPath() + `fastsearchproduct?value=${payload}&storeCode=${getStoreCode()}`,
        headers: {
            Accept: "application/json; charset=utf-8"
        },
        success: function (response) {
            commit(types.RECEIVED_SEARCH_RESULT, response);

            // stop loading
            commit(types.UPDATE_SEARCH_LOADING, false);
        }
    });
}

export function searchActionOption({commit}, payload) {
    $.ajax({
        url: getDefaultAPIPath() + `fastsearchproduct?value=${payload.searchValue}&storeCode=${getStoreCode()}&filterAttribute=${payload.filterAttribute}`,
        headers: {
            Accept: "application/json; charset=utf-8"
        },
        success: function (response) {
            commit(types.RECEIVED_SEARCH_RESULT_OPTION, response);

            // stop loading
            commit(types.UPDATE_SEARCH_LOADING_OPTION, false);
        }
    });
}

/**
 * qty simple product change for grouped product type
 * @param commit
 * @param payload
 */
export function qtySimpleProductChange({commit}, payload) {
    commit(types.QTY_SIMPLE_PRODUCT_CHANGE, payload);
}

/**
 * add to cart handle
 * @param commit
 * @param payload
 */
export function addToCartChange({commit, state}) {
    const formDataCart = new FormData();
    // push row default as cvs
    formDataCart.append('param', JSON.stringify(state.productList));

    // call service create order
    createOrderAPI(formDataCart, response => {
        // clear productList in localstorage
        clearProductListFromLocalStorage();
        // redirect to cart page
        window.location.href = '/checkout/cart';
    });
}

/**
 * add to cart and checkout
 * @param commit
 * @param state
 */
export function addToCartToCheckout({commit, state}) {
    const formDataCart = new FormData();
    // push row default as cvs
    formDataCart.append('param', JSON.stringify(state.productList));

    // call service to add to cart
    createOrderAPI(formDataCart, response => {
        if (response === 1) {
            // clear productList in localstorage
            clearProductListFromLocalStorage();
            // redirect to checkout page
            window.location.href = '/checkout';
        }
    });
}

/**
 * write newest product list to local storage
 * @param state
 */
export function syncProductListLocalStorage({state}) {
    window.localStorage.setItem(types.FAST_ORDER_LOCAL_STORAGE, JSON.stringify(state.productList));
}

/**
 * read product list from local storage
 */
export function readProductFromLocalStorage({commit}) {
    const productList = window.localStorage.getItem(types.FAST_ORDER_LOCAL_STORAGE);
    const productListJson = JSON.parse(productList);
    if (productListJson && productListJson.length > 0) {
        commit(types.SET_TO_PRODUCT_LIST, {productList: productListJson});
    }
}



