<template>
  <div :class="styles.wrapContainer">
    <div :class="styles.wrapTitle">
      <h4>{{ configMage.translate.text_fastorder }}</h4>
    </div>
    <search-product />
    <span v-if="this.isLoadingCsvUploading">Uploading...</span>
    <div :class="styles.wrapProductList">
      <ProductList />
    </div>
    <div :class="styles.wrapButtonAction">
      <button
        :disabled="getAddToCartBtnStatus()"
        :style="this.buttonStyle()"
        type="submit"
        class="action primary"
        @click="this.addToCart"
      >
        {{ getAddToCartText() }}
      </button>
      <button
        :disabled="getCheckoutBtnStatus()"
        @click="this.checkout"
      >
        {{ getCheckoutText() }}
      </button>
    </div>
    <div :class="styles.wrapFooterAction">
      <div :class="styles.wrapLeftAction">
        <div v-if="configEnabledAddMultipleSku()">
          <p>{{configMage.translate.text_enter_multiple_skus}}</p>
          <textarea
            v-model="inputMultipleSku"
            @input="onInputMultipleSku"
          />
          <div v-html="configMage.translate.text_enter_multiple_skus_note" :class="styles.wrapSampleText">
          </div>
          <button
            id="fast-order-btn-add-multiple-sku"
            :disabled="this.isAddToListProcessing"
            @click="this.addToList"
          >
            {{ isAddToListProcessing ? `${configMage.translate.text_add_to_list}...` : configMage.translate.text_add_to_list }}
          </button>
        </div>
      </div>
      <div :class="styles.wrapRightAction">
        <div v-if="configIsEnabledUploadCsv() === '1'">
          <p>{{configMage.translate.text_add_from_file}}</p>
          <button
            :disabled="this.isUploadCsvProcessing"
            @click="this.openFileInput"
          >
            {{ isUploadCsvProcessing ? `${configMage.translate.text_choose_file}...` : configMage.translate.text_choose_file }}
          </button>
          <p :class="styles.wrapDownloadFile">
            <a
              href="/loffastforder/fastorder/downloadcsv"
              download
            >{{configMage.translate.text_download_a_samle_csv_file}}</a>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import $ from 'jquery';
    import {mapState} from 'vuex';
    import ProductList from './components/product-list/ProductList';
    import styles from './init.scss';
    import SearchProduct from './components/search-product/SearchProduct';

    export default {
        components: {
            ProductList,
            'search-product': SearchProduct
        },
        props: ['configMage'],
        data() {
            return {
                inputMultipleSku: '',
                styles,
                disabledAddToListButton: false
            }
        },
        computed: {
            ...mapState({
                productList: state => state.productList,
                isLoadingCsvUploading: state => state.isLoadingCsvUploading,
                isAddToListProcessing: state => state.isAddToListProcessing,
                isAddingToCartProcessing: state => state.isAddingToCartProcessing,
                isCheckoutProcessing: state => state.isCheckoutProcessing,
                isUploadCsvProcessing: state => state.isUploadCsvProcessing,
                settings: state => state.settings
            }),
        },
        mounted() {
            // read file from local first
            this.$store.dispatch('readProductFromLocalStorage');
            // bootstrap and pass config param from main.js init
            this.$store.dispatch('bootstrap', this.configMage);
            // add event catch file onchange
            this.catchFileOnChange();

            // apply default test data
            // this.$store.dispatch('applyTestData');
        },
        methods: {
            buttonStyle() {
                const designSettings = this.settings[3];
                const colorBackgroundButton = designSettings ? designSettings.color_background_button : '';
                const buttonColor = designSettings ? designSettings.button_color : 'ffffff';
                return {
                    backgroundColor: `#${colorBackgroundButton}`,
                    color: `#${buttonColor}`,
                    border: `1px solid #${colorBackgroundButton}`
                }
            },
            configIsEnabledUploadCsv() {
                const generalSettings = this.settings[0];
                return generalSettings ? generalSettings.enable_upload_csv : 0;
            },
            configEnabledAddMultipleSku() {
                const generalSettings = this.settings[0];
                const enabledMultiProductSku = generalSettings ? generalSettings.enable_multi_productsku : 0;
                return enabledMultiProductSku === '1';
            },
            onInputMultipleSku(e) {
                const value = e.target.value;
                // set validate validInput
                if (value.length > 0) {
                    this.$set(this, 'disabledAddToListButton', false);
                } else {
                    this.$set(this, 'disabledAddToListButton', true);
                }
            },
            submitUploadFile(event) {
                if (event) event.preventDefault();
                // start upload loading
                this.$store.dispatch('startLoadingUploadFile');

                // call upload file handling
                setTimeout(() => {
                    this.$store.dispatch('uploadCsvFile');
                }, 200);

                return false
            },
            openFileInput() {
                $('#csv_file').click();
            },
            catchFileOnChange() {
                $('#csv_file').on('change', e => {
                    // submit upload file
                    this.submitUploadFile();
                });
            },
            addToList() {
                // start loading and disabled AddToList button
                this.$store.dispatch('startLoadingAddToList');

                // add multiple sku process
                this.$store.dispatch('addMultipleProductSku', {data: this.inputMultipleSku});
            },
            addToCart() {
                // start loading for addToCart button
                this.$store.dispatch('startLoadingAddToCart');

                // handle add to cart
                this.$store.dispatch('addToCartChange');
            },
            checkout() {
                // start loading
                this.$store.dispatch('startLoadingCheckout');

                // checkout processing
                this.$store.dispatch('addToCartToCheckout');
            },
            showSearchResultPanel() {
                this.$set(this, 'isShowSearchResultPanel', true);
            },
            hideSearchResultPanel() {
                this.$set(this, 'isShowSearchResultPanel', false);
            },
            /**
             *
             * @returns {boolean} return true for disabling or not
             */
            getAddToCartBtnStatus() {
                if (this.productList.length > 0) {
                    // return disable state with isAddingToCartProcessing
                    return this.isAddingToCartProcessing;
                } else {
                    return 1;
                }
            },
            getAddToCartText() {
              const addToCartText = this.configMage.translate.text_add_to_cart;
              if (this.getAddToCartBtnStatus() === 1) {
                    return addToCartText;
                } else if (this.getAddToCartBtnStatus() === true) {
                    return `${addToCartText}...`;
                } else {
                    return addToCartText;
                }
            },
            getCheckoutBtnStatus() {
                if (this.productList.length > 0) {
                    // return disable state with isAddingToCartProcessing
                    return this.isCheckoutProcessing;
                } else {
                    return 1;
                }
            },
            getCheckoutText() {
              const textCheckout = this.configMage.translate.text_checkout;
                if (this.getCheckoutBtnStatus() === 1) {
                    return textCheckout;
                } else if (this.getCheckoutBtnStatus() === true) {
                    return `${textCheckout}...`;
                } else {
                    return textCheckout;
                }
            }
        }
    }
</script>



