import {mapState} from 'vuex';
import {getAllUrlParams} from '../helpers/common-helper';
import {basePathDev} from '../store/service';
import LocaleCurrency from 'locale-currency';

// define a mixin object
const fastOrderMixin = {
    /**
     * props
     */
    props: {
        optionLabel: {
            type: Object,
            default: function () {
                return {
                    "fast_option_selected": [
                        {
                            selection_id: 0,
                            selection_product_qty: 0
                        }
                    ]
                }
            }
        },
        selection: {
            type: Array,
            default: function () {
                return []
            }
        },
        productIndex: {type: Number, default: 0},
        optionLabelId: {
            type: String,
            default: '0'
        }
    },
    methods: {
        mixRenderProductImage: function (item) {
            if(item.image) {
                const localParam = getAllUrlParams().local;
                let domain = this.domain;
                if (localParam == 1) {
                    return `${basePathDev}/pub/media/catalog/product/${item.image}`;
                }
                return `${this.getHttps()}${domain}/pub/media/catalog/product/${item.image}`;
            } else {
                return 'data:image/png;base64,';
            }
        },
        getHttps: function() {
            if (location.protocol !== 'https:')
            {
                return 'http://';
            }
            return 'https://';
        },
        /**
         * change qty of product
         * @param index
         * @param manualInput
         * @param manualValue
         */
        mixQtyOnChange: function (value, index, manualInput = false, manualValue = 0) {
            if (manualInput) {
                this.$store.dispatch('updateQty', {index, qty: manualValue});
            } else {
                this.$store.dispatch('updateQty', {index, qty: value});
            }
        },
        mixRemoveProductItem: function (index) {
            this.$store.dispatch('removeItem', {index});
        },
        mixDuplicateItem: function (item) {
            const itemAssign = JSON.parse(JSON.stringify(item));
            this.$store.dispatch('duplicateItem', {item: itemAssign});
        },
        // find selection have default value equal 1
        mixFindSelectionIdDefault() {
            this.selection.forEach(item => {
                if (item.selection_is_default === '1') {
                    this.mixSetSelected(item);
                    return; // just return, selection_id was set above line
                }
                return false;
            });
        },
        /**
         * set selected item when dropdown has changed
         * @param selectionId
         * @returns {boolean}
         */
        mixSetSelectedItem(selectionId) {
            // none select
            if (selectionId === '-1') {
                this.$store.dispatch('productBundleUpdateSelected', {
                    productIndex: this.productIndex,
                    optionLabelId: this.optionLabelId,
                    selectedItem: {}
                });
            } else {
                // get selection match with selectionId
                this.selection.forEach(item => {
                    if (item.selection_id === selectionId) {
                        this.$store.dispatch('productBundleUpdateSelected', {
                            productIndex: this.productIndex,
                            optionLabelId: this.optionLabelId,
                            selectedItem: item
                        });
                        return true
                    }
                });
            }
            return true;
        },
        mixSetSelected(item) {
            // update to mutation to update
            this.$store.dispatch('productBundleUpdateSelected', {
                productIndex: this.productIndex,
                optionLabelId: this.optionLabelId,
                selectedItem: item
            });
        },
        mixSelectQtyInputChange(value) {
            this.$store.dispatch('updateQtyBundleProduct', {
                productIndex: this.productIndex,
                optionLabelId: this.optionLabelId,
                qty: value
            });
        },
        formatCurrency(finalPrice) {
            if(this.currencyCode) {
                const locale = LocaleCurrency.getLocales(this.currencyCode)[0];
                const formatter = new Intl.NumberFormat(locale, {
                    style: 'currency',
                    currency: this.currencyCode
                });
                return formatter.format(finalPrice);
            }
        }
    },
    computed: {
        ...mapState({
            domain: state => state.domain,
            currencyCode: state => state.currencyCode,
            config: state => state.config
        })
    }
};

export default fastOrderMixin



