<template>
  <fragment>
    <div :class="stylesGrid.wrapQty">
      <div :class="stylesGrid.countDown"
           @click="this.counterUpQty">
        <span>-</span>
      </div>
      <div :class="stylesGrid.wrapInput">
        <label>
          <input
            :value="item.fast_csv_qty"
            type="number"
            min="0" step="1"
            @blur="onMouseBlur"
            onkeypress='return event.charCode >= 48 && event.charCode <= 57'
            @input="qtyOnChange($event, index)"
          >
        </label>
      </div>
      <div :class="stylesGrid.countUp"
           @click="this.counterDownQty">
        <span>+</span>
      </div>
    </div>
  </fragment>
</template>

<script>
    import stylesGrid from '../grid.scss';
    import fastOrderMixin from '../fast-order-mixin';

    export default {
        name: "Qty",
        mixins: [fastOrderMixin],
        props: {
          item: {
            type: Object,
            default: function () {
              return {}
            }
          },
          index: {
            type: Number,
            default: 0
          }
        },
        data: function () {
          return {
            stylesGrid
          }
        },
        methods: {
          onMouseBlur() {
            // if qty input is empty, let reset input = 0 value
            if(this.item.fast_csv_qty.trim().length === 0) {
              // check qty input and reset to 0
              this.mixQtyOnChange(0, this.index);
            }
          },
          qtyOnChange($event, index) {
            const value = $event.target.value;
            if(value > -1) {
              this.mixQtyOnChange($event.target.value, index);
            }
          },
          counterUpQty() {
            const newQty = parseInt(this.item.fast_csv_qty) - 1;
            if(newQty > -1) {
              this.mixQtyOnChange(0,this.index, true, newQty);
            }
          },
          counterDownQty() {
            const newQty = parseInt(this.item.fast_csv_qty) + 1;
            this.mixQtyOnChange(0, this.index, true, newQty);
          }
        }
    }
</script>


