import * as types from './constants';

const mutation = {
    [types.SET_TO_PRODUCT_LIST](state, payload) {
        state.productList = state.productList.concat(payload.productList);
    },
    [types.UPDATE_QTY_BY_ITEM](state, payload) {
        const qty = payload.qty;
        const index = payload.index;
        state.productList[index].fast_csv_qty = qty;
    },
    [types.REMOVE_ITEM](state, payload) {
        const index = payload.index;
        state.productList.splice(index, 1);
    },
    [types.DUPLICATE_ITEM](state, payload) {
        state.productList = state.productList.concat(payload.item);
    },
    [types.CONFIGURATION_ON_CHANGE_SELECTED](state, payload) {
        const optionIndex = payload.optionIndex;
        state.productList[payload.productIndex].fast_active_options[optionIndex].fast_selected = payload.value
    },
    [types.RELOAD_CONFIGURATION_OPTIONS_BY_PRODUCT_ID](state, payload) {
        state.productList[payload.productIndex].fast_product_active = payload.productActive;
    },
    [types.UPDATE_DOMAIN](state, payload) {
        state.domain = payload.domain;
    },
    [types.PRODUCT_BUNDLE_UPDATE_SELECTED](state, payload) {
        const productIndex = payload.productIndex;
        const optionLabelId = payload.optionLabelId;
        let selectedItem = payload.selectedItem;

        if (!Array.isArray(selectedItem)) {
            // convert to array
            selectedItem = [selectedItem];
        }

        state.productList[productIndex].fast_option_label[optionLabelId].fast_option_selected = selectedItem;
    },
    [types.PUSH_TO_PRODUCT_LIST](state, payload) {
        state.productList = state.productList.concat(payload);
    },
    [types.RECEIVED_SEARCH_RESULT](state, payload) {
        state.searchResults = payload;
    },
    [types.RECEIVED_SEARCH_RESULT_OPTION](state, payload) {
        state.searchResultsOption = payload;
    },
    [types.UPDATE_QTY_BUNDLE_PRODUCT](state, payload) {
        const productIndex = payload.productIndex;
        const optionLabelId = payload.optionLabelId;
        state.productList[productIndex].fast_option_label[optionLabelId].fast_option_selected[0].selection_product_qty = payload.qty;
    },
    [types.QTY_SIMPLE_PRODUCT_CHANGE](state, payload) {
        const productIndex = payload.productIndex;
        const simpleProductIndex = payload.simpleProductIndex;
        state.productList[productIndex].fast_grouped_products[simpleProductIndex].qty = payload.value;
    },
    [types.UPDATE_SEARCH_LOADING](state, payload) {
        state.searchLoading = payload;
    },
    [types.UPDATE_SEARCH_LOADING_OPTION](state, payload) {
        state.searchLoadingByOption = payload;
    },
    [types.RECEIVED_SETTINGS](state, payload) {
        state.settings = payload.response;
    },
    [types.RECEIVED_CONFIG](state, payload) {
        state.config = payload;
    },
    [types.UPDATE_LOADING_ADD_TO_LIST](state, payload) {
        state.isAddToListProcessing = payload;
    },
    [types.UPDATE_LOADING_ADD_TO_CART](state, payload) {
        state.isAddingToCartProcessing = payload;
    },
    [types.UPDATE_LOADING_CHECKOUT](state, payload) {
        state.isCheckoutProcessing = payload;
    },
    [types.UPDATE_LOADING_UPLOAD_FILE](state, payload) {
        state.isUploadCsvProcessing = payload;
    },
    [types.RECEIVED_CURRENCY](state, payload) {
        state.currencyCode = payload;
    }
};

export default mutation



