<template>
  <fragment>
    <div :class="stylesGrid.wrapAction">
      <div
        title="Duplicate"
        :class="[stylesGrid.wrapDuplicate, stylesGrid.wrapSvg]"
        @click="mixDuplicateItem(item)">
        <duplicate />
      </div>
      <div
        title="Remove"
        :class="[stylesGrid.wrapCloseIcon, stylesGrid.wrapSvg]"
        @click="mixRemoveProductItem(index)">
        <close-icon />
      </div>
    </div>
  </fragment>
</template>

<script>
    import stylesGrid from '../grid.scss';
    import fastOrderMixin from '../fast-order-mixin';
    import closeIcon from '../icons/close';
    import duplicate from "../icons/duplicate";

    export default {
        name: "Action",
        components: {
          'close-icon': closeIcon,
          'duplicate': duplicate,
        },
        mixins: [fastOrderMixin],
        props: {
          item: {
            type: Object,
            default: function() {
              return {}
            }
          },
          index: {
            type: Number,
            default: 0
          }
        },
        data: function () {
            return {
                stylesGrid
            }
        }
    }
</script>


