<template>
  <fragment>
    <div :class="stylesGrid.wrapRow" data-row-data="1">
      <div :class="stylesGrid.wrapImage">
        <img
          :src="mixRenderProductImage(item)"
        >
      </div>
      <div :class="stylesGrid.wrapProductName">
        <a target="_blank" :href="item.product_url" v-html="item.name"></a>
      </div>
      <div :class="stylesGrid.wrapSku">
        {{ item.sku }}
      </div>
      <qty :index="index"
           :item="item"/>
      <div :class="stylesGrid.wrapSubTotal">
        {{ sumPrice() }}
      </div>
      <action
        :item="item"
        :index="index"
      />
    </div>
  </fragment>
</template>

<script>
    import fastOrderMixin from '../fast-order-mixin';
    import Currency from 'currency.js';
    import stylesGrid from '../grid.scss';
    import Action from '../common/Action';
    import Qty from '../common/Qty';
    import DropDown from './custom-options/Select';
    import RadioButtons from './custom-options/RadioButtons';
    import Checkbox from './custom-options/Checkbox';
    import multipleSelect from './custom-options/MutipleSelect';
    import Input from './custom-options/Input';
    import Textarea from './custom-options/Textarea';
    import {mapState} from 'vuex';
    import arrowDown from '../icons/arrow-down';
    import arrowUp from '../icons/arrow-up';

    export default {
      name: "ConfigurableProduct",
      components: {
        'action': Action,
        'qty': Qty,
        'input-field': Input,
        'textarea-field': Textarea,
        'drop-down': DropDown,
        'checkbox': Checkbox,
        'multiple-select': multipleSelect,
        'arrow-down': arrowDown,
        'arrow-up': arrowUp,
      },
      mixins: [fastOrderMixin],
      props: ['item', 'index'],
      data() {
        return {
          stylesGrid,
          Action
        }
      },
      computed: {
        ...mapState({
          currencyCode: state => state.currencyCode
        })
      },
      methods: {
        sumPrice() {
          const finalPrice = Currency(this.item.base_price_value, {formatWithSymbol: true}).multiply(this.item.fast_csv_qty);
          return this.formatCurrency(finalPrice);
        }
      }
    }
</script>



