<template>
  <div :class="styles.wrapSearchResultAbsolute">
    <ul>
      <li
        v-for="(item, index) in this.searchResults"
        :key="index"
        @click="addProductFromSearch(item, index)"
      >
        <search-item
          :item="item"
          :index="index"
        />
      </li>
    </ul>
  </div>
</template>

<script>
  import SearchItem from "./SearchItem";
  import $ from "jquery";
  import styles from './search-box-result.scss';

    export default {
      name: "SearchBoxResult",
      components: {
        'search-item': SearchItem
      },
      props: ['searchResults'],
      data() {
        return {
          styles }
      },
      methods: {
        addProductFromSearch(item, index) {
          const newItem = `${item.sku}:1`;
          // Start loading for adding product item
          $(`span[data-loading-index=${index}]`).show();
          this.$store.dispatch('addMultipleProductSku', {data: newItem, index});
        }
      }
    }
</script>


