<template>
  <fragment>
    <div :class="styles.wrapOptionList">
      <div
        v-for="(item, optionIndex) in attrOptions"
        :class="styles.wrapOption"
        :key="optionIndex">
        <label>{{ item.fast_frontend_label }}</label>
        <select
          :value="item.fast_selected"
          @change="optionOnChange($event, optionIndex)"
        >
          <option
            v-for="itemValue in item.fast_options"
            :value="Object.keys(itemValue)[0]"
          >
            {{ Object.values(itemValue)[0] }}
          </option>
        </select>
      </div>
    </div>
  </fragment>
</template>

<script>
  import styles from './configurable-option.scss';

    export default {
        name: "ConfigurableOption",
        props: ['product', 'productIndex'],
        data() {
            return {
                attrOptions: [],
                styles
            }
        },
        mounted() {
            this.$set(this, 'attrOptions', this.product.fast_active_options);
        },
        methods: {
            optionOnChange(event, optionIndex) {
              this.$store.dispatch('configurationOnChangeSelected', {productIndex: this.productIndex, optionIndex, value: event.target.value});
            }
        } // pass props item from product list
    }
</script>


