<template>
  <div
    :class="stylesGrid.wrapRow"
    data-row-data="1"
    data-dow-product-type="bundle"
  >
    <div :class="stylesGrid.wrapImage">
      <img
        v-if="item.image"
        :src="mixRenderProductImage(item)"
      >
    </div>
    <div
      :class="stylesGrid.wrapProductName"
      data-product-type="bundle"
      :data-option-bundle-open="dataOptionBundleOpen"
    >
      <!--  Basic display without option showing-->
      <div
        v-if="!dataOptionBundleOpen"
        :class="stylesGrid.wrapBundleShow"
      >
        <div :class="stylesGrid.wrapShowProductName">
          <a
            target="_blank"
            :href="item.product_url"
            v-html="item.name"
          />
        </div>
        <div
          :class="stylesGrid.wrapArrowDown"
          @click="this.showOption"
        >
          <arrow-down />
        </div>
      </div>
      <!--  Full option settings displaying-->
      <div v-show="dataOptionBundleOpen">
        <div :class="stylesGrid.wrapOnShowProductName">
          <div :class="stylesGrid.wrapName">
            <a
              target="_blank"
              :href="item.product_url"
            >{{ item.name }}</a>
          </div>
          <div :class="stylesGrid.wrapSku">
            <a
              target="_blank"
              :href="item.product_url"
            >{{ item.name }}</a>
          </div>
          <div :class="stylesGrid.wrapHideOption">
            <div @click="this.hideOption">
              <arrow-up />
            </div>
          </div>
        </div>
        <ul :class="stylesGrid.wrapListSelectionOption">
          <li
            v-for="(option, optionIndex) in fastOptionLabel"
            :key="optionIndex"
            :class="stylesGrid.wrapListSelectionOption"
          >
            <div :class="formStyle.wrapLabel">
              <div>{{ option.option_title }}</div>
            </div>
            <div v-if="option.option_type === 'select'">
              <drop-down
                :product-index="index"
                :option-label-id="option.option_id"
                :option-label="option"
                :selection="fastSelectionArray[option.option_id]"
              />
            </div>
            <div v-else-if="option.option_type === 'radio'">
              <radio-buttons
                :product-index="index"
                :option-label-id="option.option_id"
                :option-label="option"
                :selection="fastSelectionArray[option.option_id]"
              />
            </div>
            <div v-else-if="option.option_type === 'checkbox'">
              <checkbox
                :product-index="index"
                :option-label-id="option.option_id"
                :option-label="option"
                :selection="fastSelectionArray[option.option_id]"
              />
            </div>
            <div v-else-if="option.option_type === 'multi'">
              <multiple-select
                :product-index="index"
                :option-label-id="option.option_id"
                :option-label="option"
                :selection="fastSelectionArray[option.option_id]"
              />
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div
      :class="stylesGrid.wrapSku"
      data-product-type="bundle"
      :data-option-bundle-open="dataOptionBundleOpen"
    >
      {{ item.sku }}
    </div>
    <qty
      :index="index"
      :item="item"
    />
    <div :class="stylesGrid.wrapSubTotal">
      {{ sumPrice() }}
    </div>
    <action
      :item="item"
      :index="index"
    />
  </div>
</template>

<script>
    import {sortDescByField} from '../../helpers/common-helper';
    import {mapState} from 'vuex';
    import DropDown from './bundle-options/Select';
    import RadioButtons from './bundle-options/RadioButtons';
    import Checkbox from './bundle-options/Checkbox';
    import multipleSelect from './bundle-options/MutipleSelect';
    import fastOrderMixin from '../fast-order-mixin';
    import Currency from 'currency.js';
    import stylesGrid from '../grid.scss';
    import arrowDown from '../icons/arrow-down';
    import arrowUp from '../icons/arrow-up';
    import formStyle from '../form.scss';
    import Action from '../common/Action';
    import Qty from '../common/Qty';

    export default {
        name: "BundleProduct",
        components: {
            'drop-down': DropDown,
            'radio-buttons': RadioButtons,
            'checkbox': Checkbox,
            'multiple-select': multipleSelect,
            'arrow-down': arrowDown,
            'arrow-up': arrowUp,
            'action': Action,
            'qty': Qty
        },
        mixins: [fastOrderMixin],
        props: {
            item: {
                type: Object,
                default: function () {
                    return {}
                }
            },
            index: {
                type: Number,
                default: 0
            }
        },
        data: function () {
            return {
                itemMain: this.item,
                fastOptionLabel: [],
                fastSelectionArray: [],
                stylesGrid,
                dataOptionBundleOpen: false,
                formStyle
            }
        },
        computed: {
            ...mapState({
                productList: state => state.productList
            })
        },
        mounted() {
            this.reSortDesc(this.itemMain);
        },
        methods: {
            reSortDesc(item) {
                const fastOptionLabel = sortDescByField(item.fast_option_label, 'position');
                this.$set(this, 'fastOptionLabel', fastOptionLabel);
                this.$set(this, 'fastSelectionArray', item.fast_selection_array);
            },
            sumPrice() {
                let finalPrice = 0;
                this.fastOptionLabel.forEach(item => {
                    const fastSelected = item.fast_option_selected;

                    // if have selected item
                    if (fastSelected.length > 0) {
                        switch (item.option_type) {
                            case 'select':
                            case 'radio':
                                finalPrice = Currency(fastSelected[0].selection_base_price_value, {formatWithSymbol: true}).multiply(fastSelected[0].selection_product_qty).add(finalPrice);
                                break;
                            case 'checkbox':
                            case 'multi':
                                fastSelected.forEach(itemCheckBox => {
                                    finalPrice = Currency(itemCheckBox.selection_base_price_value, {formatWithSymbol: true}).multiply(itemCheckBox.selection_product_qty).add(finalPrice);
                                });
                                break;
                            default:
                                break;
                        }
                    }
                });

                // when end of loop, get final price x main qty
                finalPrice = Currency(finalPrice, {formatWithSymbol: true}).multiply(this.item.fast_csv_qty);

                // return final price
                return this.formatCurrency(finalPrice);
            },
            showOption() {
                this.$set(this, 'dataOptionBundleOpen', true);
            },
            hideOption() {
                this.$set(this, 'dataOptionBundleOpen', false);
            }
        }
    }
</script>



