import $ from "jquery";
import {getAllUrlParams} from "../helpers/common-helper";

export const basePathDev = 'http://m2extension.localhost';

/**
 * get store id
 * @returns {*|jQuery|string|undefined}
 */
function getStoreCode() {
    let storeCode = $('#fast-order-store-code').val();
    if(storeCode == '' || !storeCode){
        storeCode = 'default';
    }
    return storeCode;
}

function getDefaultAPIPath()
{
    const localParam = getAllUrlParams().local;
    const storeCode =  getStoreCode();
    const defaultAPIPath = '/rest/'+storeCode+'/V1/lof-fastorder/';
    if (localParam == 1) {
        return `${basePathDev}${defaultAPIPath}`
    }

    return defaultAPIPath;
}

/**
 * create order
 * @param formDataCart
 * @param callback
 */
export function createOrderAPI(formDataCart, callback)
{
    $.ajax({
        url: getDefaultAPIPath() + 'fastcreateorder',
        type: 'POST',
        data: formDataCart,
        cache: false,
        processData: false,
        contentType: false,
        success: function (response) {
            callback(response);
        }
    });
}

/**
 * get settings
 * @param callback
 */
export function getSettingsAPI(callback)
{
    $.ajax({
        url: getDefaultAPIPath() + `fastordersetting?path=loffastorder`,
        headers: {
            Accept: "application/json; charset=utf-8"
        },
        success: function (response) {
            callback(response);
        }
    });
}

/**
 * get current customer group
 * @param callback
 */
export function getCurrentCustomerGroupAPI(callback)
{
    $.ajax({
        url: `${getDefaultAPIPath()}getcurrentgroup`,
        headers: {
            Accept: "application/json; charset=utf-8"
        },
        success: function (response) {
            callback(response);
        }
    });
}

/**
 * get currency api
 * @param callback
 */
export function getCurrencyAPI(callback) {
    $.ajax({
        url: `${getDefaultAPIPath()}getcurrency`,
        headers: {
            Accept: "application/json; charset=utf-8"
        },
        success: function (response) {
            callback(response);
        }
    });
}



