<template>
  <div :class="formStyle.formGroup">
    <div>
      <div
        v-for="(item, index) in selection"
        :key="index"
        :class="formStyle.formCheck"
      >
        <input
          :id="`checkbox-${item.selection_id}`"
          v-model="listChecked"
          :class="formStyle.formCheckInput"
          :name="`chk-box${optionLabel.option_id}`"
          type="checkbox"
          :value="item.selection_id"
          @change="checkBoxOnChange"
        >
        <label
          :class="formStyle.formCheckLabel"
          :for="`checkbox-${item.selection_id}`"
        >{{
          item.selection_product_qty }} x {{
          item.selection_product_name }} + <strong>{{ item.selection_product_price_format }}</strong></label>
      </div>
    </div>
  </div>
</template>

<script>
    import fastOrderMixin from '../../fast-order-mixin';
    import formStyle from '../../form.scss';

    export default {
        name: "Checkbox",
        mixins: [fastOrderMixin],
        data() {
            return {
                listChecked: [],
                formStyle
            }
        },
        mounted() {
            this.findCheckBoxDefault();
        },
        methods: {
            checkBoxOnChange(e) {
                const newListChecked = [];
                this.selection.forEach(item => {
                    // exists in list checked
                    if (this.listChecked.indexOf(item.selection_id) !== -1) {
                        newListChecked.push(item);
                    }
                });

                // update new list selection to option label
                this.mixSetSelected(newListChecked);
            },
            findCheckBoxDefault() {
                const arrayDefault = [];
                // array select for multiple select
                const arrSelected = [];
                this.selection.forEach(item => {
                    if (item.selection_is_default === '1') {
                        arrayDefault.push(item);
                        arrSelected.push(item.selection_id);
                    }
                });

                // set active selection with array value
                this.mixSetSelected(arrayDefault);
                this.$set(this, 'listChecked', arrSelected);
            },
        }
    }
</script>



