<template>
    <div>
        <div :class="formStyle.formGroup">
            <select @change="selectOnChange">
                <option v-if="optionLabel.require === '0'" value="-1">
                    Choose a selection...
                </option>
                <option
                        v-for="(item, index) in selection"
                        :key="index"
                        :selected="(optionLabel.fast_option_selected.length > 0 ? optionLabel.fast_option_selected[0].selection_id : 0) === item.selection_id"
                        :value="item.selection_id">
                    {{ item.selection_product_name }} +{{ item.selection_product_price_format }}
                </option>
            </select>
        </div>
        <div :class="formStyle.formGroup">
            <label :for="`qty-input${optionLabel.option_id}`">Quantity</label>
            <div :class="stylesGrid.wrapQty">
                <div :class="stylesGrid.countDown" @click="counterDownQty">
                    <span>-</span>
                </div>
                <div :class="stylesGrid.wrapInput">
                    <input
                            :id="`qty-input${optionLabel.option_id}`"
                            :class="formStyle.minQty"
                            type="number"
                            min="0" step="1"
                            @blur="this.inputQtyOnBlur"
                            onkeypress='return event.charCode >= 48 && event.charCode <= 57'
                            :value="optionLabel.fast_option_selected.length > 0 ? optionLabel.fast_option_selected[0].selection_product_qty : 0"
                            @input="counterInputChange"
                    >
                </div>
                <div :class="stylesGrid.countUp" @click="counterUpQty">
                    <span>+</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import fastOrderMixin from '../../fast-order-mixin';
    import formStyle from '../../form.scss';
    import stylesGrid from '../../grid.scss';

    export default {
        name: 'Select',
        mixins: [fastOrderMixin],
        /**
         * data
         * @returns {{selected: {}}}
         */
        data: function () {
            return {
                selected: {},
                formStyle,
                stylesGrid
            }
        },
        mounted() {
            // get selection is default
            this.mixFindSelectionIdDefault();
        },
        /**
         * methods
         */
        methods: {
            inputQtyOnBlur() {
                const qty = this.optionLabel.fast_option_selected[0].selection_product_qty;
                if (qty === undefined || qty === '') {
                    // set default = 0
                    this.mixSelectQtyInputChange(0);
                }
            },
            /**
             *
             * @param evt
             */
            async selectOnChange(evt) {
                const selectionId = evt.target.value;
                // find item with selection_id
                this.mixSetSelectedItem(selectionId);
            },
            counterDownQty() {
                const counterUpValue = this.optionLabel.fast_option_selected[0].selection_product_qty - 1;
                if (counterUpValue > -1) {
                    this.mixSelectQtyInputChange(counterUpValue);
                }
            },
            counterUpQty() {
                const counterUpValue = this.optionLabel.fast_option_selected[0].selection_product_qty + 1;
                this.mixSelectQtyInputChange(counterUpValue);
            },
            counterInputChange($event) {
                const value = $event.target.value;
                this.mixSelectQtyInputChange(value);
            },
        }
    }
</script>


