<template>
  <div :class="styles.wrapProductList">
    <div :class="stylesGrid.grid">
      <div
        :class="stylesGrid.wrapRow"
        data-label="1"
        :style="getHeadingBackground()"
      >
        <div :class="stylesGrid.wrapImage">
          <label>{{config.translate.text_products}}</label>
        </div>
        <div :class="stylesGrid.wrapProductName" />
        <div
          :class="stylesGrid.wrapSku"
          data-hide-on-mobile="true"
        >
          <label>{{config.translate.text_sku}}</label>
        </div>
        <div
          :class="stylesGrid.wrapQty"
          data-hide-on-mobile="true"
        >
          <label>{{config.translate.text_qty}}</label>
        </div>
        <div
          :class="stylesGrid.wrapSubTotal"
          data-hide-on-mobile="true"
        >
          <label>{{config.translate.text_subtotal}}</label>
        </div>
        <div
          :class="stylesGrid.wrapAction"
          data-hide-on-mobile="true"
        >
          <label>{{config.translate.text_action}}</label>
        </div>
      </div>
    </div>
    <div
      v-for="(item, index) in this.productList"
      :key="index"
      :data-last-child="index === productList.length - 1 ? 1 : 0"
      :class="stylesGrid.grid"
    >
      <configurable-product
        v-if="item.type_id === 'configurable'"
        :item="item"
        :index="index"
      />
      <bundle-product
        v-else-if="item.type_id === 'bundle'"
        :item="item"
        :index="index"
      />
      <group-product
        v-else-if="item.type_id === 'grouped'"
        :item="item"
        :index="index"
      />
      <simple-product
        v-else
        :item="item"
        :index="index"
      />
    </div>
    <div :class="stylesGrid.wrapFooter">
      <div />
      <div :class="stylesGrid.wrapFooterRight">
        <div :class="stylesGrid.wrapTotalQty">
          <div :class="stylesGrid.wrapTotalQtyLabel">
            <span>{{config.translate.text_total_qty}}</span>
          </div>
          <div :class="stylesGrid.wrapTotalQtyValue">
            <span>{{ totalQty }}</span>
          </div>
        </div>
        <div :class="stylesGrid.wrapSubTotalFooter">
          <div :class="stylesGrid.wrapLabelSubTotal">
            <span>{{config.translate.text_sub_total}}</span>
          </div>
          <div :class="stylesGrid.wrapSubTotalValue">
            {{ calTotalSubTotal() }}
            <span>{{ totalSubTotal }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import {mapState} from 'vuex';
    import styles from './product-list.scss';
    import stylesGrid from '../grid.scss';
    import SimpleProduct from '../product-types/SimpleProduct';
    import ConfigurableProduct from '../product-types/ConfigurableProduct';
    import BundleProduct from '../product-types/BundleProduct';
    import GroupProduct from "../product-types/GroupedProduct";
    import fastOrderMixin from '../fast-order-mixin';
    import Currency from 'currency.js';
    import {sortDescByField} from '../../helpers/common-helper';

    export default {
        name: 'ProductList',
        components: {
            'simple-product': SimpleProduct,
            'configurable-product': ConfigurableProduct,
            'bundle-product': BundleProduct,
            'group-product': GroupProduct
        },
        mixins: [fastOrderMixin],
        data() {
            return {
                styles,
                stylesGrid,
                searchTimeout: null,
                totalQty: 0,
                totalSubTotal: 0,
                searchValue: ''
            }
        },
        computed: {
            ...mapState({
                productList: state => state.productList,
                domain: state => state.domain,
                searchResults: state => state.searchResults,
                settings: state => state.settings
            })
        },
        watch: {
            productList: {
                handler: function () {
                    // re calc sub total
                    // this.calTotalSubTotal();
                    // sync to local storage
                    this.$store.dispatch('syncProductListLocalStorage');
                },
                deep: true
            }
        },
        mounted() {
            // this.calTotalSubTotal();
        },
        methods: {
            getHeadingBackground() {
                const designSettings = this.settings[3];
                const colorHeadingBackground = designSettings ? designSettings.color_heading_background : 'ffffff';
                const colorHeadingText = designSettings ? designSettings.color_heading_text : '707070';
                return {
                    backgroundColor: `#${colorHeadingBackground}`,
                    color: `#${colorHeadingText}`
                }
            },
            /**
             * this function will same with sum price each component
             */
            calTotalSubTotal() {
                let finalPrice = 0;
                let totalQty = 0;

                this.productList.forEach(item => {
                    switch (item.type_id) {
                        case 'simple':
                        case 'downloadable': {
                            const price = Currency(item.base_price_value, {formatWithSymbol: true}).multiply(item.fast_csv_qty);
                            finalPrice = Currency(finalPrice, {formatWithSymbol: true}).add(price);
                            totalQty = this.counterQty(totalQty, item.fast_csv_qty);
                            break;
                        }
                        case 'configurable': {
                            const price = Currency(item.fast_product_active.base_price_value, {formatWithSymbol: true}).multiply(item.fast_csv_qty);
                            finalPrice = Currency(finalPrice, {formatWithSymbol: true}).add(price);
                            totalQty = this.counterQty(totalQty, item.fast_csv_qty);
                            break;
                        }
                        case 'bundle': {
                            const fastOptionLabel = sortDescByField(item.fast_option_label, 'position');

                            let totalPriceBundle = 0;
                            fastOptionLabel.forEach(item => {
                                const fastSelected = item.fast_option_selected;
                                // if have selected item
                                if (Object.entries(fastSelected).length > 0) {
                                    switch (item.option_type) {
                                        case 'select':
                                        case 'radio':
                                            const price = Currency(fastSelected[0].selection_base_price_value, {formatWithSymbol: true}).multiply(fastSelected[0].selection_product_qty);
                                            totalPriceBundle = Currency(totalPriceBundle, {formatWithSymbol: true}).add(price);
                                            break;
                                        case 'checkbox':
                                        case 'multi':
                                            fastSelected.forEach(itemCheckBox => {
                                                const price = Currency(itemCheckBox.selection_base_price_value, {formatWithSymbol: true}).multiply(itemCheckBox.selection_product_qty);
                                                totalPriceBundle = Currency(totalPriceBundle, {formatWithSymbol: true}).add(price);
                                            });
                                            break;
                                        default:
                                            break;
                                    }
                                }
                            });

                            totalQty = this.counterQty(totalQty, item.fast_csv_qty);
                            totalPriceBundle = Currency(totalPriceBundle, {formatWithSymbol: true}).multiply(item.fast_csv_qty);
                            finalPrice = Currency(finalPrice, {formatWithSymbol: true}).add(totalPriceBundle);
                            break;
                        }
                        case 'grouped': {
                            item.fast_grouped_products.forEach(item => {
                                if (item.qty > 0) {
                                    const price = Currency(item.base_price_value, {formatWithSymbol: true}).multiply(item.qty);
                                    finalPrice = Currency(finalPrice, {formatWithSymbol: true}).add(price);
                                    totalQty = this.counterQty(totalQty, item.qty);
                                }
                            });
                            break;
                        }
                        default:
                            break;
                    }
                });
                this.$set(this, 'totalQty', totalQty);
                this.$set(this, 'totalSubTotal', this.formatCurrency(finalPrice));
            },
            /**
             * add qty
             * @param mainQty
             * @param newAddQty
             */
            counterQty(mainQty, newAddQty) {
                const tempNewQty = newAddQty ? parseInt(newAddQty) : 0;
                return mainQty + tempNewQty;
            }
        }
    }
</script>



