<template>
    <div>
        <div :class="formStyle.formGroup">
            <fragment
                    v-for="(item, index) in selection"
                    :key="index">
                <!--v-if have not required will render radio with none option-->
                <div v-if="optionLabel.require === '0' && index === 0" :class="formStyle.formCheck">
                    <input :id="`${renderCheckBoxName()}-1`"
                           :class="formStyle.formCheckInput"
                           type="radio"
                           value="-1"
                           :checked="getOptionSelected() === -1"
                           @change="radioOnChange">
                    <label :class="formStyle.formCheckLabel" :for="`${renderCheckBoxName()}-1`">None</label>
                </div>
                <div :class="formStyle.formCheck">
                    <input :id="`${renderCheckBoxName()}${item.selection_id}`" :class="formStyle.formCheckInput"
                           type="radio"
                           :value="item.selection_id"
                           :checked="getOptionSelected() === item.selection_id"
                           @change="radioOnChange"
                    >
                    <label :class="formStyle.formCheckLabel" :for="`${renderCheckBoxName()}${item.selection_id}`">{{
                        item.selection_product_name }} +<strong>{{ item.selection_product_price_format
                            }}</strong></label>
                </div>
            </fragment>
        </div>
        <div :class="formStyle.formGroup">
            <label :for="`qty-input${optionLabel.option_id}`">Quantity</label>
            <div :class="stylesGrid.wrapQty">
                <div :class="stylesGrid.countDown" @click="counterDownQty">
                    <span>-</span>
                </div>
                <div :class="stylesGrid.wrapInput">
                    <input
                            :id="`qty-input${optionLabel.option_id}`"
                            type="number"
                            min="0" step="1"
                            :class="formStyle.minQty"
                            @blur="this.inputQtyOnBlur"
                            onkeypress='return event.charCode >= 48 && event.charCode <= 57'
                            :value="(optionLabel.fast_option_selected.length > 0 ? optionLabel.fast_option_selected[0].selection_product_qty : 1)"
                            @input="counterInputChange"
                    >
                </div>
                <div :class="stylesGrid.countUp" @click="counterUpQty">
                    <span>+</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import fastOrderMixin from '../../fast-order-mixin';
    import formStyle from '../../form.scss';
    import stylesGrid from '../../grid.scss';

    export default {
        name: "Input",
        mixins: [fastOrderMixin],
        data: function () {
            return {
                formStyle,
                stylesGrid
            }
        },
        mounted() {
            this.mixFindSelectionIdDefault();
        },
        methods: {
            inputQtyOnBlur() {
                const qty = this.optionLabel.fast_option_selected[0].selection_product_qty;
                if (qty === undefined || qty === '') {
                    // set default = 0
                    this.mixSelectQtyInputChange(0);
                }
            },
            radioOnChange(evt) {
                this.mixSetSelectedItem(evt.target.value);
            },
            renderCheckBoxName() {
                return `checkbox-${this.productIndex}`;
            },
            counterDownQty() {
                const counterUpValue = this.optionLabel.fast_option_selected[0].selection_product_qty - 1;
                if (counterUpValue > -1) {
                    this.mixSelectQtyInputChange(counterUpValue);
                }
            },
            counterUpQty() {
                const counterUpValue = this.optionLabel.fast_option_selected[0].selection_product_qty + 1;
                this.mixSelectQtyInputChange(counterUpValue);
            },
            counterInputChange($event) {
                const value = $event.target.value;
                this.mixSelectQtyInputChange(value);
            },
            getOptionSelected() {
                const optionSelectionId = this.optionLabel.fast_option_selected.length > 0 ? this.optionLabel.fast_option_selected[0].selection_id : 0;
                if (optionSelectionId === undefined) {
                    return -1;
                }
                return optionSelectionId;
            }
        }
    }
</script>


